/* src/components/GardenGrid.css */
.grid-container-wrapper {
  width: 80vw; /* or a fixed size like 800px */
  height: 60vh; /* or a fixed size like 600px */
  margin: 20px auto;
  border: 1px solid #333;
  position: relative;
  overflow: hidden;
  background: #f0f0f0;
  overscroll-behavior: contain;
}

.grid-container {
  position: relative;
  background: #fff;
}

.grid-cell {
  width: 100px;
  height: 100px;
  border: 1px solid #aaa;
  box-sizing: border-box;
  float: left;
  position: relative;
}

.grid-cell.selected {
  background-color: lightgreen;
}

.veg-label {
  font-size: 14px;
  position: absolute;
  bottom: 0;
  right: 0;
  background: rgba(255, 255, 255, 0.7);
  pointer-events: none;
  padding: 2px;
}

.current-selection {
  position: absolute;
  border: 2px dashed red;
  background-color: rgba(255, 0, 0, 0.2);
  pointer-events: none;
}

.mode-toggle {
  text-align: center;
  margin-bottom: 10px;
}

.dropdown-container select {
  font-size: 14px;
}
